
.col{
    display: block;
    box-flex:1; -webkit-box-flex:1;
    -ms-flex-positive:0;
     flex-grow:0;
    -ms-flex-negative:1;
        flex-shrink:1;
    -ms-flex-preferred-size:33%;
        flex-basis:33%;
    /*flex-basis:auto;*/
    /*min-width:33%;*/
  text-align: center;
  padding:0.5rem;
}
.addlong{
    -ms-flex-preferred-size:50%;
        flex-basis:50%;
}
.col .tag{
  display: inline-block;
  width:100%;  line-height: 3rem;
  font-size: 1.2rem;
  padding:0.5rem ;
  border-radius:6px;
  overflow: hidden;
  color:#545454;
  background: white;
}
.col .tag.active{
    color:white;
    background: #B91C36;
}
.col .tag a{
  display: line-block;
  max-width: 100%;
  overflow-x: hidden;
}




