
.banner {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 4rem;
  z-index: 5;
}
.banner .banner-wrap {
  width: 100%;
  max-width: 640px;
  height: 4rem;
  line-height: 4rem;
  font-size: 2rem;
  margin: 0 auto;
  color: #fff;
  text-align: center;
  background: #b91c36;
}


