
.result{
  position: fixed;
  left:0;bottom:0;top:0;right:0;
  width:100%;
}
.result-wrap{
  position: relative;
  width:100%;
  height: 100%;
  max-width:640px;
  margin:0 auto;
  padding-top:7rem;
}
.list-wrap{
  position: absolute;
  top:7rem; left:0; right:0; bottom:0;
}
.list-wrap .no-result{
  position: absolute;
  top:0;left:0;
  display: none;
  width:100%; height:3rem; line-height: 3rem;
  text-align:center;
  font-size: 1.4rem;
}
.scroller-wrap{
  width:100%; height:100%;
}
.scroller-move{
}
.result-list{
  padding: 0 1rem 3rem 1rem;
    width:100%;
}



