
.type-list {
  width: 100%;
}
.type-list>.list-wrap {
  width: 90%;
  margin: 2rem auto 0 auto;
  background: #fff;
}
.type-list>.list-wrap .list-btn {
  width: 100%;
  height: 4rem;
  line-height: 4rem;
  font-size: 1.6rem;
  text-align: center;
  border-radius: 99px;
  margin-bottom: 3rem;
  border: 1px solid #f00;
}


